.dropdown__icons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 20px;
  padding-top: 27px;

  border-top: 1px solid $color-solitude;

  @include viewport--lg {
    border-top: none;
  }
}

.dropdown__icon-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 262px;
  margin-bottom: 20px;
}

.dropdown__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
}

.dropdown__icon-text {
  width: 179px;
  margin: 0;
  padding: 0;

  font-size: 14px;
  line-height: 18px;
}

.dropdown__inner {
  /* 1200px - 60px = 1140px
  table - 838px = 73.5%
  pdf - 302px = 26.5% */

  display: flex;
  flex-direction: row;
  margin-top: 18px;
}

.dropdown__table {
  width: 73.5%;
  font-size: 14px;
  line-height: 18px;

  @include viewport--lg {
    margin-bottom: 100px;
  }
}

.dropdown__table-tr {
  display: flex;
  margin-bottom: 18px;
  padding-bottom: 6px;

  border-bottom: 1px solid $color-solitude;

  @include viewport--sm {
    flex-direction: column;
  }

  &--no-border {
    border: none;
  }

  &--margin {
    margin-bottom: 5px;
  }

  &--row {
    @include viewport--sm {
      flex-direction: row;
    }
  }
}

.dropdown__table-td-title {
  display: flex;
  width: 447px;
  padding-right: 85px;

  font-weight: 600;

  @include viewport--sm {
    width: 100%;
    padding-right: 0;
  }

  &--light {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
  }
}

.dropdown__table-td-data {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  max-width: 350px;
  padding-left: 7px;

  @include viewport--md {
    padding-left: 0;
  }

  &--svg {
    @include viewport--md {
      align-items: flex-start;

      svg {
        margin-top: 2px;
      }
    }
  }

  span {
    margin-right: 5px;

    @include viewport--md {
      max-width: 200px;
    }
  }
}

.dropdown__pfd {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 26.5%;
  background-color: $color-ghost-white;
}

.dropdown__pfd-link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  img {
    z-index: 1;
  }

  .dropdown__pfd-text {
    position: absolute;
    bottom: 5px;
    margin: 0 auto;
    width: 100px;

    font-weight: 600;
    font-size: 14px;
    line-height: 15px;
    color: $color-alizarin;
    text-align: center;

    background-color: $color-default-white;
    box-shadow: 0 0 5px 5px $color-default-white;
    z-index: 2;

    @include viewport--sm {
      display: none;
    }
  }
}
