.reviews {
  padding-top: 73px;
  padding-bottom: 90px;

  background-color: $color-ghost-white;

  @include viewport--md {
    padding-bottom: 101px;
    overflow: hidden;
  }

  @include viewport--sm {
    padding-top: 21px;
    padding-bottom: 50px;
  }

  h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;

    @include viewport--md {
      margin-bottom: 2px;
      margin-top: 21px;

      font-size: 28px;
      line-height: 34px;
    }

    @include viewport--sm {
      font-size: 24px;
      line-height: 31px;
    }
  }
}

.reviews__wrapper {
  display: flex;
  flex-wrap: wrap;

  @include viewport--md {
    flex-direction: column;
  }
}

.reviews__write-btn {
  height: min-content;
  align-self: center;
  margin-left: auto;

  @include viewport--md {
    order: 2;

    width: 100%;
    margin-top: 15px;
  }

  @include viewport--sm {
    margin-top: 10px;
  }

  button {
    min-width: 200px;
    width: auto;

    @include viewport--md {
      min-width: 313px;
      margin: 0 auto;
    }

    @include viewport--sm {
      min-width: 100%;
    }
  }
}

.reviews__slider {
  position: relative;

  max-width: 100%;
  margin-top: 15px;

  @include viewport--md {
    margin-left: -1.95vw;
  }

  @include viewport--sm {
    margin-left: -2.66vw;
    margin-top: 10px;
  }
}

.reviews__slide {
  display: flex;
  justify-content: space-between;
  padding: 40px;

  background-color: $color-default-white;
  border-radius: 10px;

  @include viewport--md {
    flex-direction: column;
    width: 70.05vw;
    padding: 1.95vw;

    background: none;
  }

  @include viewport--sm {
    width: 84vw;
    padding: 2.66vw;
  }
}

.reviews__logo {
  display: flex;
  width: 80px;
  height: 80px;
  margin-bottom: 23px;

  border: 1px solid $color-solitude;
  border-radius: 10px;

  @include viewport--md {
    order: 2;

    width: 50px;
    height: 50px;
    margin-bottom: 0;
    margin-top: 3px;
  }

  @include viewport--sm {
    margin-top: 0;
  }

  img {
    object-fit: contain;
    max-height: 100%;
    margin: auto;
  }
}

.reviews__inner-wrapper {
  @include viewport--md {
    padding: 6px 5.6% 24px;

    background-color: $color-default-white;
    border-radius: 0 0 10px 10px;
  }

  @include viewport--sm {
    padding: 2px 4.6% 16px;

    font-size: 14px;
    line-height: 18px;
  }

  blockquote {
    margin: 0 15% 0 0;

    @include viewport--md {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin: 0;
    }
  }

  cite {
    display: flex;
    flex-direction: column;
    margin-top: 30px;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;

    @include viewport--md {
      flex-basis: 70%;
      margin-top: 8px;
    }

    @include viewport--sm {
      margin-top: -1px;

      font-size: 16px;
      line-height: 21px;
    }
  }

  span {
    margin-top: 4px;

    font-weight: 400;
    font-size: 16px;

    @include viewport--sm {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.reviews__picture {
  width: 437px;
  height: 285px;
  flex-shrink: 0;

  border-radius: 10px;

  overflow: hidden;

  @include viewport--xl {
    width: 22.76vw;
    height: 14.84vw;
  }

  @include viewport--md {
    order: -1;

    height: 38.54vw;
    width: 100%;

    border-radius: 10px 10px 0 0;
  }

  @include viewport--sm {
    height: 52vw;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}
