.guarantee__title {
  margin: 0;
  padding: 0;

  font-size: 24px;
  line-height: 29px;

  @include viewport--sm {
    font-size: 18px;
    line-height: 24px;
  }
}

.guarantee__list {
  margin: 0;
  margin-top: 32px;
  padding: 0;
  list-style: none;
}

.guarantee__item {
  position: relative;
  max-width: 1200px;
  width: 100%;
  margin-bottom: 17px;
  padding: 25px 30px;
  padding-bottom: 0;

  border: 1px solid $color-solitude;
  border-radius: 10px;

  @include viewport--lg {
    padding-right: 0;
  }

  @include viewport--sm {
    border: 1px solid;
    padding-right: 30px;
  }

  .guarantee__btn-get--right {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 230px;
    height: 60px;
    margin: 0;

    @include viewport--lg {
      top: auto;
      bottom: 20px;
      min-width: auto;
    }

    @include viewport--sm {
      top: auto;
      bottom: 20px;
      right: 0;
      left: 0;
      margin: 0 auto;
      min-width: 90%;
    }
  }

  .guarantee__dropdown-btn--bottom {
    @include viewport--lg {
      position: absolute;
      bottom: 40px;
      margin: 0;
    }

    @include viewport--sm {
      top: auto;
      bottom: 90px;
    }
  }
}

.guarantee__content {
  display: flex;
  flex-direction: row;
  padding-bottom: 30px;
}

.guarantee__item-col {
  display: flex;
  flex-direction: column;
  /* 1200px - 60px = 1140px
  table-and-base - 699px = 61.3%
  icons - 272px = 23.6% */

  &--base {
    width: 61.3%;

    @include viewport--lg {
      width: 100%;
    }
  }

  /* table-and-base - 699px = 100%
  base - 268px = 38.34%
  table - 431px = 61.65% */

  &--icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 23.6%;
    padding-left: 50px;

    border-left: 1px solid $color-solitude;

    @include viewport--lg {
      display: none;
    }
  }

  &--pdf {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 15.1%;

    background-color: $color-ghost-white;

    @include viewport--sm {
      background-color: $color-default-white;
    }

    img {
      width: 13px;
      height: 162px;

      @include viewport--lg {
        width: 120px;
        height: 163px;
      }

      @include viewport--sm {
        width: 69px;
        height: 94px;
      }
    }
  }
}

.guarantee__desc {
  @include viewport--lg {
    flex-direction: column;
    padding-bottom: 67px;
    border-bottom: 1px solid $color-solitude;
  }

  @include viewport--sm {
    padding-bottom: 0;
    border-bottom: none;
  }
}

.guarantee__desc-info {
  display: flex;
  flex-direction: column;
  width: 38.34%;
  padding-right: 20px;

  &--btn {
    @include viewport--lg {
      order: 2;
    }
  }

  @include viewport--sm {
    width: 100%;
  }
}

.guarantee__desc-data {
  display: flex;
  flex-direction: column;
  width: 61.65%;
  padding-right: 20px;

  &--table {
    @include viewport--lg {
      order: 1;
      width: 50%;
    }

    @include viewport--sm {
      width: 100%;
    }
  }
}

.guarantee__desc-table {
  @include viewport--sm {
    display: flex;
    flex-direction: column;
  }
}

.guarantee__item-title {
  margin: 0;
  padding: 0;

  font-weight: 600;
  font-size: 20px;
  line-height: 24px;

  @include viewport--sm {
    font-size: 16px;
    line-height: 21px;
  }
}

.guarantee__item-subtitle {
  width: max-content;
  margin: 0;
  margin-top: 10px;
  padding: 4px 9px;

  font-size: 14px;
  line-height: 18px;
  color: $color-default-white;
  text-align: center;

  background: $color-cello;
  border-radius: 5px;

  @include viewport--tn {
    font-size: 12px;
    line-height: 21px;
  }
}

.guarantee__btn-get {
  max-width: 226px;
  width: 100%;
  margin: 0;
  margin-top: 40px;
  padding: 19px 0;
}

.guarantee__item-desc {
  width: 382px;
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 21px;

  @include viewport--xxl {
    width: 100%;
  }

  @include viewport--lg {
    margin-top: 30px;
  }
}

.guarantee__table {
  margin-top: 20px;

  font-size: 14px;
  line-height: 18px;
}

.guarantee__table-title {
  width: 144px;
  font-weight: 600;
}

.guarantee__dropdown-btn {
  margin-top: 10px;

  @include viewport--lg {
    margin-top: 46px;
  }

  @include viewport--sm {
    margin-top: 10px;
  }
}

.guarantee__dropdown-btn-text {
  margin-right: 3px;
}

.guarantee__icon-up svg {
  transform: rotate(180deg);
}

.guarantee__tooltip {
  margin-right: 15px;
  margin-bottom: 15px;
}

.guarantee__pdf-link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  img {
    z-index: 1;
  }

  .guarantee__pdf-text {
    position: absolute;
    bottom: 5px;
    margin: 0 auto;
    width: 100px;

    font-weight: 600;
    font-size: 14px;
    line-height: 15px;
    color: $color-alizarin;
    text-align: center;

    background-color: $color-default-white;
    box-shadow: 0 0 5px 5px $color-default-white;
    z-index: 2;

    @include viewport--sm {
      display: none;
    }
  }
}
