.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;

  @include viewport--md {
    min-width: 0;
  }

  &--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.scroll-lock {
  position: fixed;

  overflow-y: auto;
}

.btn-reset {
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 60px;
  width: max-content;
  min-width: 220px;
  padding: 17px 33px;

  font-weight: 600;
  border-radius: 10px;
  outline: none;

  @include viewport--md {
    min-width: 314px;
  }

  @include viewport--sm {
    min-width: 100%;
    width: 100%;
  }
}

.btn-white {
  border: 1px solid $color-alizarin;
  color: $color-alizarin;

  opacity: 1;
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    opacity: 0.5;
  }
}

.btn-red {
  width: max-content;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: $color-alizarin;

  border-bottom: 1px dashed $color-alizarin-2;
  transition: border-bottom $default-transition-settings;

  &:hover,
  &:focus {
    border-bottom: 1px dashed $color-alizarin;
  }

  &:active {
    color: $color-fire-engine-red;
    border-bottom: 1px dashed $color-fire-engine-red;
  }
}

.btn-grey {
  width: 50px;
  height: 50px;

  background-color: $color-ghost-white;
  border-radius: 12px;
}

.btn-accented {
  background-color: $color-alizarin;
  color: $color-default-white;

  opacity: 1;
  transition: color $default-transition-settings,
    opacity $default-transition-settings,
    background-color $default-transition-settings;

  &:hover,
  &:focus {
    background-color: $color-fire-engine-red;
    color: transparentize($color-default-white, 0.2);
  }

  &:active {
    background-color: $color-fire-engine-red-2;
    color: transparentize($color-default-white, 0.4);
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.btn-icon {
  height: 40px;
  min-width: 40px;
  width: max-content;
  padding: 0;

  border-radius: 5px;
  background-color: $color-ghost-white;
}

.btn-link {
  opacity: 1;
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  &:disabled {
    opacity: 0.5;
  }
}

.row {
  display: flex;
  flex-direction: row;
  width: 100%;
}

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}
