.footer {
  border-top: 1px solid $color-solitude;
}

.footer__contact-us,
.footer__contact-btns,
.footer__copyright {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.footer__contact-us {
  padding-top: 34px;
  padding-bottom: 34px;

  border-bottom: 1px solid $color-solitude;

  @include viewport--md {
    align-items: flex-start;
    padding-top: 70px;
    padding-bottom: 38px;
  }

  p {
    font-size: 18px;
    line-height: 27px;
  }
}

.footer__contact-text {
  flex-basis: 50%;
  flex-grow: 1;
  display: flex;
  align-items: center;
  min-width: 520px;

  @include viewport--md {
    flex-basis: 46%;
    flex-direction: column;
    min-width: 0;
    flex-grow: 0;
  }

  p {
    @include viewport--md {
      margin-top: 0;
      margin-bottom: 9px;
    }
  }
}

.footer__contact-phone {
  flex-basis: 156px;
  display: flex;
  flex-direction: column;
  margin-right: 5.4%;
  margin-left: 3.5%;
  flex-shrink: 0;

  color: $color-raven;
  font-size: 14px;
  line-height: 18px;

  @include viewport--md {
    width: 290px;
    flex-basis: auto;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 0;
    margin-right: auto;
  }

  a {
    color: $color-nero;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.footer__contact-btns {
  flex-basis: 395px;
  margin-left: auto;

  @include viewport--md {
    flex-basis: 44%;
    display: flex;
    flex-direction: column;
  }

  a {
    min-width: 150px;
    padding-right: 22.5px;
    padding-left: 22.5px;
    margin-top: 5px;
    margin-bottom: 5px;

    @include viewport--md {
      width: 100%;
      margin: 0;
    }

    &:first-of-type {
      min-width: 225px;

      @include viewport--md {
        margin-bottom: 14px;
        margin-top: 9px;
      }
    }
  }
}

.footer__nav {
  padding-top: 44px;
  padding-bottom: 11px;

  @include viewport--md {
    padding-top: 22px;
    padding-bottom: 0;
  }
}

.footer__copyright {
  padding-top: 31px;
  padding-bottom: 31px;

  font-size: 14px;
  line-height: 18px;

  border-top: 1px solid $color-solitude;

  a {
    margin-left: 32px;
    color: $color-dodger-blue;

    transition: color $default-transition-settings;

    @include viewport--md {
      margin-left: 0;
    }

    &:hover,
    &:focus {
      text-decoration: underline;
    }

    &:active {
      color: $color-navy-blue;
    }
  }
}

.footer__copyright-text {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  max-width: 60%;

  @include viewport--md {
    flex-direction: column;
    align-items: flex-start;
  }

  p {
    @include viewport--md {
      margin-top: 0;
      margin-bottom: 8px;
    }
  }
}

.footer__designed-by {
  display: flex;
  align-items: center;
  padding: 13px 32px;
  margin-left: auto;

  background: $color-ghost-white;
  border-radius: 10px;

  transition: background-color $default-transition-settings;

  a {
    margin-right: 0;
    margin-left: 7px;
  }

  &:hover,
  &:focus-within {
    background-color: darken($color-ghost-white, 2%);
  }
}
