.breadcrumbs {
  position: relative;

  @include viewport--md {
    padding-top: 15px;
    padding-bottom: 15px;

    &::after {
      content: "";
      display: block;
      position: absolute;
      height: 1px;
      width: 150vw;
      bottom: 0;
      left: -25vw;

      background-color: $color-solitude;
    }
  }

  @include viewport--sm {
    padding-bottom: 10px;
    padding-top: 10px;

    &::before {
      content: "";
      display: block;
      position: absolute;
      height: 1px;
      width: 150vw;
      top: 0;
      left: -25vw;

      background-color: $color-solitude;
    }
  }

  ul {
    display: flex;
    align-items: center;

    list-style: none;
    padding: 0;
    margin: 0;
  }
}

.breadcrumbs__link {
  display: flex;
  align-items: center;

  color: $color-raven;
  font-size: 14px;
  line-height: 18px;


  &:hover,
  &:focus {
    text-decoration: underline;
  }

  &::after {
    content: "";
    width: 16px;
    height: 22px;
    margin-left: 8px;
    margin-right: 5px;

    background: url("../img/svg/breadcrumbs-arrow.svg") center center no-repeat;
  }

  &--current {
    &::after {
      content: none;
    }
  }
}
