.problems-solved {
  padding-top: 75px;
  padding-bottom: 103px;

  background-color: $color-ghost-white;

  @include viewport--md {
    padding-bottom: 80px;
  }

  @include viewport--sm {
    padding-top: 27px;
    padding-bottom: 29px;
  }

  ul {
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    margin-top: 34px;

    list-style: none;

    @include viewport--md {
      flex-direction: column;
    }

    @include viewport--sm {
      margin-top: 23px;
    }
  }

  h2 {
    @include viewport--md {
      margin-top: 15px;

      font-size: 28px;
      line-height: 34px;
      letter-spacing: 0.2px;
    }

    @include viewport--sm {
      font-size: 24px;
      line-height: 31px;
    }
  }
}

.problems-solved__card {
  position: relative;

  display: flex;
  width: 380px;
  height: 278px;
  max-width: 26.57vw;
  max-height: 20.14vw;

  border-radius: 10px;

  @include viewport--md {
    width: 100%;
    height: min-content;
    max-height: none;
    max-width: none;
    margin-bottom: 20px;

    background-color: $color-default-white;
    overflow: hidden;
  }

  @include viewport--sm {
    flex-direction: column;
    height: auto;
    max-height: none;
  }
}

.problems-solved__picture {
  height: 100%;
  width: 100%;

  border-radius: 10px;
  overflow: hidden;

  @include viewport--md {
    width: 50%;
    height: 30.08vw;

    background-color: $color-default-white;
  }

  @include viewport--sm {
    height: 40vw;
    width: 100%;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}

.problems-solved__text {
  position: absolute;
  left: 0;
  bottom: 14%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 89.47%;
  min-height: 74px;
  padding: 2% 5%;

  background-color: $color-default-white;
  border-radius: 0 10px 10px 0;

  // transition: top $default-transition-settings,
  //   width $default-transition-settings,
  //   height $default-transition-settings;

  @include viewport--md {
    position: static;

    justify-content: flex-start;
    padding: 3.7% 3.3% 2% 4.5%;
    width: 50%;
  }

  @include viewport--sm {
    width: 100%;
    padding: 4.2% 5.5% 7.5% 5.5%;

    border-radius: 0 0 10px 10px;
  }

  p {
    margin: 0;
  }
}

.problems-solved__text-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  font-weight: 600;
  font-size: 20px;
  line-height: 24px;

  @include viewport--sm {
    position: relative;

    font-size: 16px;
    line-height: 21px;
  }

  svg {
    width: 40px;
    max-width: 13.4%;
    margin-left: 4px;
    margin-bottom: 5px;
    flex-shrink: 0;

    @include viewport--sm {
      position: absolute;
      top: 8%;
      right: 0;
    }
  }

  p {
    @include viewport--sm {
      max-width: 70%;
    }
  }
}

.problems-solved__description {
  display: none;

  @include viewport--md {
    display: initial;
  }

  @include viewport--sm {
    margin-right: auto;
  }

  span {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 112px;
    min-width: max-content;
    height: 34px;
    padding: 5px 10px;
    margin-top: 9px;
    margin-bottom: 9px;

    color: $color-alizarin;
    font-weight: 600;

    background-color: $color-ghost-white;
    border-radius: 5px;

    @include viewport--md {
      margin-top: 14px;
      margin-bottom: 16px;
    }

    @include viewport--sm {
      margin-top: 16px;
      margin-bottom: 15px;
    }

    &::before {
      content: "";
      width: 18px;
      height: 100%;

      background: url("../img/svg/mark.svg") center center no-repeat;
    }
  }

  p {
    @include viewport--sm {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.problems-solved__card:hover {
  @include viewport--lg-min {
    .problems-solved__text-header {
      align-self: flex-start;
      max-width: 305px;
    }

    .problems-solved__picture {
      border-radius: 12px 12px 10px 10px;
    }

    .problems-solved__text {
      top: 0;
      bottom: auto;

      width: 100%;
      min-height: 80.5%;
      border-radius: 10px;
    }

    .problems-solved__description {
      display: initial;
    }
  }
}

