.feedback {
  padding-top: 100px;
  margin-bottom: 97px;

  @include viewport--md {
    margin-bottom: 100px;
  }

  @include viewport--sm {
    padding-top: 50px;
    margin-bottom: 50px;
  }
}

.feedback__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 50px;

  border: 1px solid $color-solitude;
  border-radius: 10px;

  @include viewport--md {
    flex-direction: column;
    align-items: flex-start;
    padding: 33px 3.8% 40px;
  }

  @include viewport--sm {
    padding: 24px 5%;
  }
}

.feedback__title {
  flex-basis: 100%;

  h2 {
    width: 70.5%;
    margin: -4px 0 36px auto;

    letter-spacing: 0.2px;

    @include viewport--md {
      width: 100%;
      margin-bottom: 33px;

      font-size: 28px;
      line-height: 34px;
    }

    @include viewport--sm {
      max-width: 80%;
      margin-left: 0;
      margin-bottom: 9px;

      font-size: 24px;
      line-height: 31px;
    }
  }
}

.feedback__form {
  width: 70.5%;

  @include viewport--md {
    width: 100%;
    margin-top: 29px;
  }

  @include viewport--sm {
    margin-top: 22px;
  }

  form {
    display: flex;
    flex-direction: column;
  }

  textarea {
    min-height: 150px;

    @include viewport--sm {
      min-height: 100px;
    }
  }

  .form__btns {
    margin-top: 18px;

    @include viewport--md {
      margin-top: 26px;
    }

    @include viewport--sm {
      margin-top: 11px;
    }
  }

  button {
    min-width: 49%;
    margin-right: 15px;

    @include viewport--md {
      min-width: 48%;
      max-width: none;
    }

    @include viewport--sm {
      margin-right: 0;
    }
  }

  small {
    max-width: 260px;
    margin-right: auto;

    font-size: 12px;
    line-height: 16px;
    color: $color-matterhorn;

    @include viewport--md {
      flex-basis: 48%;
      max-width: none;
      margin-right: 0;
      margin-left: 0;

      font-size: 14px;
      line-height: 18px;
    }

    @include viewport--sm {
      display: flex;
      flex-direction: column;
      margin-right: auto;
      margin-top: 15px;

      font-size: 12px;
      line-height: 16px;
    }
  }

  a {
    color: $color-dodger-blue;
    transition: color $default-transition-settings;

    &:hover,
    &:focus {
      text-decoration: underline;
    }

    &:active {
      color: $color-navy-blue;
    }
  }
}

.feedback__input-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  @include viewport--md {
    flex-direction: column;
    margin-bottom: 0;
  }

  .form__item {
    flex-basis: 49.03%;

    @include viewport--md {
      flex-direction: column;
      margin-bottom: 20px;
    }

    @include viewport--sm {
      margin-bottom: 15px;
    }
  }
}

.feedback__picture {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-grow: 1;
  margin-right: 6%;

  @include viewport--md {
    flex-direction: row;
    margin-left: 2px;

    text-align: left;
  }

  img {
    width: 150px;
    height: 150px;
    max-width: none;

    border-radius: 50%;
    object-fit: cover;

    @include viewport--md {
      width: 100px;
      height: 100px;
    }

    @include viewport--sm {
      width: 60px;
      height: 60px;
    }
  }
}

.feedback__author {
  max-width: 160px;
  margin-top: 21px;

  @include viewport--md {
    max-width: none;
    margin-left: 20px;
    margin-top: 3px;
  }

  @include viewport--sm {
    max-width: 50%;
    margin-left: 13px;
  }

  b {
    font-size: 18px;

    @include viewport--md {
      font-size: 20px;
    }

    @include viewport--sm {
      font-size: 16px;
      line-height: 21px;
    }
  }

  p {
    margin-top: 7px;
    margin-bottom: 3px;

    @include viewport--sm {
      margin-top: 3px;

      font-size: 14px;
      line-height: 18px;
    }
  }
}
