.promo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 26px;
  margin-bottom: 102px;

  @include viewport--md {
    flex-direction: column;
    padding-top: 60px;
  }

  @include viewport--sm {
    padding-top: 17px;
    margin-bottom: 51px;
  }

  h1 {
    margin: 0;

    font-weight: 800;
    font-size: 34px;
    line-height: 41px;

    @include viewport--md {
      margin-top: 32px;
      max-width: 350px;
    }

    @include viewport--sm {
      margin-top: 22px;

      font-size: 24px;
      line-height: 31px;
    }
  }

  p {
    @include viewport--md {
      margin-top: 12px;

      font-size: 20px;
      line-height: 30px;
    }

    @include viewport--sm {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.promo__inner-wrapper {
  max-width: 468px;

  @include viewport--md {
    max-width: none;
  }

  a {
    margin-top: 33px;

    @include viewport--sm {
      margin-top: 22px;
    }
  }
}

.promo__picture {
  width: 600px;
  height: 364px;

  border-radius: 10px;

  overflow: hidden;

  @include viewport--xl {
    width: 31.25vw;
    height: 18.96vw;
  }

  @include viewport--md {
    order: -1;

    width: 100%;
    height: 47.66vw;
  }

  @include viewport--sm {
    height: 61.87vw;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}
