  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  @media (max-width: 1023px) {
    .wrapper {
      min-width: 0; } }
  .wrapper--no-scrollbar {
