    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.btn-reset {
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  outline: none; }

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 60px;
  width: max-content;
  min-width: 220px;
  padding: 17px 33px;
  font-weight: 600;
  border-radius: 10px;
  outline: none; }
  @media (max-width: 1023px) {
    .btn {
      min-width: 314px; } }
  @media (max-width: 767px) {
    .btn {
      min-width: 100%;
      width: 100%; } }

.btn-white {
  border: 1px solid #e72430;
  color: #e72430;
  opacity: 1;
  transition: opacity 0.3s ease; }
  .btn-white:hover, .btn-white:focus {
    opacity: 0.5; }

.btn-red {
  width: max-content;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: #e72430;
  border-bottom: 1px dashed #f4babd;
  transition: border-bottom 0.3s ease; }
  .btn-red:hover, .btn-red:focus {
    border-bottom: 1px dashed #e72430; }
  .btn-red:active {
    color: #d21e29;
    border-bottom: 1px dashed #d21e29; }

.btn-grey {
  width: 50px;
  height: 50px;
  background-color: #f5f6fa;
  border-radius: 12px; }

.btn-accented {
  background-color: #e72430;
  color: #ffffff;
  opacity: 1;
  transition: color 0.3s ease, opacity 0.3s ease, background-color 0.3s ease; }
  .btn-accented:hover, .btn-accented:focus {
    background-color: #d21e29;
    color: rgba(255, 255, 255, 0.8); }
  .btn-accented:active {
    background-color: #c21924;
    color: rgba(255, 255, 255, 0.6); }
  .btn-accented:disabled {
    opacity: 0.5;
    pointer-events: none; }

.btn-icon {
  height: 40px;
  min-width: 40px;
  width: max-content;
  padding: 0;
  border-radius: 5px;
  background-color: #f5f6fa; }

.btn-link {
  opacity: 1;
  transition: opacity 0.3s ease; }
  .btn-link:hover, .btn-link:focus {
    opacity: 0.8; }
  .btn-link:active {
    opacity: 0.6; }
  .btn-link:disabled {
    opacity: 0.5; }

.row {
  display: flex;
  flex-direction: row;
  width: 100%; }

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0; }
  100% {
    visibility: hidden;
    opacity: 0; } }

.container {
  width: 82%;
  max-width: 1200px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 84.38%; } }
  @media (max-width: 767px) {
    .container {
      width: 92%; } }

