.footer-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: -15px;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  a {
    display: inline-block;
    margin-bottom: 15px;
  }
}

.footer-nav__menu {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @include viewport--md {
    margin-top: 14px;
  }

  a {
    line-height: 21px;
    color: $color-raven;

    transition: color $default-transition-settings,
      background-color $default-transition-settings,
      border-color $default-transition-settings;
  }
}

.footer-nav__menu--short {
  max-width: 27%;
  margin-left: 3.7%;
  margin-right: auto;
  padding-right: 7.8%;

  @include viewport--md {
    max-width: 39%;
    margin-left: 2%;
  }

  ul {
    columns: 2 auto;
    column-gap: 65%;
    margin-right: 2.6%;
  }

  li {
    width: max-content;
    margin-bottom: 12px;
  }
}

.footer-nav__menu--unwrapped {
  width: 16.2%;
  min-width: max-content;
  margin-left: 15px;

  @include viewport--md {
    width: 22.2%;
  }

  a {
    border-bottom: 1px solid transparent;

    &:hover,
    &:focus {
      border-color: $color-raven;
    }

    &:active {
      color: $color-matterhorn;
      border-color: $color-matterhorn;
    }
  }
}

.footer-nav__link > a {
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  color: $color-nero;

  border-bottom: 1px solid $color-solitude;

  &:hover,
  &:focus {
    border-color: $color-nero;
  }

  &:active {
    color: $color-default-black;
    border-color: $color-default-black;
  }
}

.footer-nav__more-link {
  a {
    font-weight: 600;
    color: $color-alizarin;

    &:hover,
    &:focus {
      color: $color-fire-engine-red;
      border-color: $color-fire-engine-red;
    }

    &:active {
      color: $color-fire-engine-red-2;
      border-color: $color-fire-engine-red-2;
    }
  }
}

.footer-nav__socials {
  margin-left: auto;
  padding-left: 10px;

  @include viewport--md {
    margin-right: auto;
    margin-left: 15.5%;
    margin-top: 20px;
  }

  ul {
    @include viewport--md {
      display: flex;
    }
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    background-color: $color-ghost-white;
    border-radius: 5px;

    &:hover,
    &:focus {
      background-color: darken($color-ghost-white, 2%);
    }

    &:active {
      background-color: darken($color-ghost-white, 4%);
    }

    @include viewport--md {
      margin-right: 15px;
    }
  }
}
