.form__item {
  position: relative;

  input,
  textarea {
    padding: 19px 20px;
    width: 100%;

    border: 1px solid $color-solitude;
    border-radius: 10px;

    &::placeholder {
      color: $color-manatee;
      font-family: inherit;
      font-size: inherit;
    }

    &:hover,
    &:focus-within {
      border-color: $color-echo-blue;
    }
  }

  label {
    position: absolute;
    top: 0;
    left: 17px;

    opacity: 0;
    padding: 2px 6px;
    height: 20px;

    font-size: 12px;
    line-height: 16px;
    color: $color-raven;

    transform: translateY(-30%) scaleY(0.5);

    background: $color-default-white;
    transition: transform $default-transition-settings,
      opacity $default-transition-settings;
  }

  input:required + label,
  textarea:required + label {
    &::after {
      content: "*";
      margin-left: 3px;
      color: $color-alizarin;
    }
  }

  input {
    height: 60px;
  }

  span {
    position: absolute;
    bottom: -14px;

    display: none;

    font-size: 12px;
    line-height: 16px;
    color: $color-alizarin;
  }
}

.form__item.filled {
  label {
    opacity: 1;
    transform: translateY(-50%) scaleY(1);
  }
}

.form__item.validated {
  input:invalid,
  textarea:invalid {
    background-color: $color-snow;
    border-color: $color-alizarin;
  }

  input:invalid ~ span,
  textarea:invalid ~ span {
    display: block;
  }
}

.form__btns {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include viewport--md {
    margin-top: 9px;
  }

  @include viewport--sm {
    flex-direction: column;
  }
}
