.choose-garantee {
  padding-top: 100px;
  margin-bottom: 101px;

  @include viewport--md {
    margin-bottom: 78px;
  }

  @include viewport--sm {
    padding-top: 50px;
    margin-bottom: 23px;
  }
}

.choose-garantee__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 51px;

  @include viewport--md {
    flex-direction: column;
    margin-bottom: 77px;
  }

  @include viewport--sm {
    margin-bottom: 22px;
  }

  h2 {
    max-width: 384px;
    margin: 0;

    font-weight: 800;
    font-size: 34px;
    line-height: 41px;

    @include viewport--md {
      max-width: none;
      margin-top: 32px;

      font-size: 28px;
      line-height: 34px;
    }

    @include viewport--sm {
      max-width: 75%;
      margin-top: 22px;

      font-size: 24px;
      line-height: 31px;
    }
  }

  p {
    max-width: 464px;

    @include viewport--md {
      max-width: none;
      margin-top: 14px;

      font-size: 20px;
      line-height: 30px;
    }

    @include viewport--sm {
      margin-top: 11px;

      font-size: 16px;
      line-height: 24px;
    }
  }
}

.choose-garantee__picture {
  width: 533px;
  height: 364px;
  flex-shrink: 0;

  border-radius: 10px;

  overflow: hidden;

  @include viewport--xl {
    width: 27.76vw;
    height: 18.96vw;
  }

  @include viewport--md {
    width: 100%;
    height: 47.66vw;
  }

  @include viewport--sm {
    height: 61.87vw;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}

.choose-garantee__inner-wrapper {
  max-width: 517px;
  width: 61%;
  margin-right: 1.3%;
  margin-left: 8%;

  @include viewport--md {
    width: 100%;
    max-width: none;
    margin-right: 0;
    margin-left: 0;
  }
}

.choose-garantee__btns {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 28px;
  margin-bottom: 18px;

  @include viewport--sm {
    flex-direction: column;
    margin-top: 19px;
  }

  button {
    margin-top: 5px;
    margin-bottom: 5px;

    &:last-of-type {
      min-width: 277px;
    }

    @include viewport--lg {
      min-width: 180px;
      padding-left: 30px;
      padding-right: 30px;

      &:last-of-type {
        min-width: 180px;
      }
    }

    @include viewport--md {
      min-width: 314px;

      &:last-of-type {
        min-width: 314px;
      }
    }

    @include viewport--sm {
      width: 100%;
      margin-bottom: 10px;
    }
  }

  button:first-of-type {
    margin-right: 10px;
  }
}
